import { defineComponent, onMounted, ref } from 'vue';
import { useI18n } from 'vue-i18n';
import cn from 'classnames';
import useSettingsStore from '@/stores/settings';
import { useNaiveFeedback } from '@/composite/useNaiveFeedback';

import BasicLayout from '../layout/BasicLayout';
import BasicTopBar from '../layout/BasicTopBar';
import DoubleDeckerContainer from '@/components/doubleDeckerContainer/DoubleDeckerContainer';
import DeckerLayout from '../layout/DeckerLayout';
import SvgIcon from '@/components/svgIcon/SvgIcon';
import ClearData from './clearData/ClearData';

import { NButton } from 'naive-ui';

import db from '@/services/db';
import { Setting } from '@/services/db/web/settings/db';
import { getControl, needOpenDecker, renderValue } from './utils';
import { deepToRaw } from '../vue-utils';
import { settingCatogory } from './config';

import styles from './settings.module.less';

export default defineComponent({
  name: 'Settings',
  setup() {
    const { updateSettingMap } = useSettingsStore();
    const { t } = useI18n({ useScope: 'global' });

    const { message } = useNaiveFeedback();

    const allSettings = ref<Setting[][]>([]);

    const selectedCatogoryIndex = ref<number>();
    const selectedSetting = ref<Setting>();

    const init = () => {
      return Promise.all(settingCatogory.map(catogory => db.settings.getSettingsByCategory(catogory))).then(res => {
        allSettings.value = res;
      });
    };

    onMounted(() => {
      init();
    });

    const saveSetting = (setting: Setting) => {
      const { key, value } = setting;
      return db.settings.updateSettingByKey(key, value).then(() => {
        message.success(t('pages.settings.decker.topbar.saveSuccess'));
        updateSettingMap(key, value);
      }).catch(() => {
        message.error(t('pages.settings.decker.topbar.saveFailed'));
        init();
      });
    };

    const renderSettingControl = (back: () => void) => {
      if (selectedSetting.value === undefined || selectedCatogoryIndex.value === undefined) {
        back();
        return <></>;
      }
      const s = selectedSetting.value;
      const control = ref();
      getControl(s, (value) => {
        s.value = value;
      }).then(res => {
        control.value = res
        console.log(res);
      })
      return <DeckerLayout
        topBar={<BasicTopBar
          leftSlots={[<NButton
            text
            onClick={back}
          >
            {t('common.cancel')}
          </NButton>]}
          rightSlots={[<NButton
            text
            type="primary"
            onClick={() => {
              const { key } = s;
              const selectedCatogorySetting = allSettings.value[selectedCatogoryIndex.value!];
              const index = selectedCatogorySetting.findIndex(se => se.key === key);
              selectedCatogorySetting.splice(index, 1, s);
              allSettings.value.splice(selectedCatogoryIndex.value!, 1, selectedCatogorySetting);

              saveSetting(s).finally(() => {
                selectedSetting.value = undefined;
                selectedCatogoryIndex.value = undefined;
                back();
              });
            }}
          >
            {t('common.save')}
          </NButton>]}
        />}
      >
        <div class={styles.control}>
          <div>{t(s.label)}</div>
          {/* {getControl(s, (value) => {
            s.value = value;
          })} */}
          {control.value}
        </div>
      </DeckerLayout>;
    };

    const renderSettings = (go: () => void) => (<BasicLayout topBar={
      <BasicTopBar>
        <SvgIcon name="setting" /> {t('pages.settings.topbar.title')}
      </BasicTopBar>
    }>
      <h2>{t('pages.settings.topbar.title')}</h2>
      <ul class={cn(styles.list)}>
        {allSettings.value.map((settings, index) => {
          return <div class={cn(styles.catogory)}>
            {settings.filter(setting => !setting.hidden).map((setting) => {
              return <li
                key={setting.key}
                class={cn(styles.item)}
                onClick={() => {
                  if (needOpenDecker(setting)) {
                    selectedCatogoryIndex.value = index;
                    selectedSetting.value = deepToRaw(setting);
                    go();
                  }
                }}
              >
                <span
                  class={cn(styles.label)}
                >{t(setting.label)}</span>
                <span
                  class={cn(styles.value)}
                >
                  {renderValue(setting, (value) => {
                    setting.value = value;
                    saveSetting(setting);
                  })}
                </span>
              </li>;
            })}
          </div>;
        })}
        <div class={cn(styles.catogory)}>
          <ClearData />
        </div>
      </ul>
    </BasicLayout>);

    return () => <DoubleDeckerContainer
      class={cn(styles.settings)}
      maskClosable={false}
    >
      {{
        default: renderSettings,
        decker: renderSettingControl
      }}
    </DoubleDeckerContainer>;
  }
});
