/**
 * @file 清除数据菜单项
 * @author Yangholmes 2025-05-07
 */

import { defineComponent } from 'vue';
import { useI18n } from 'vue-i18n';
import cn from 'classnames';

import { useNaiveFeedback } from '@/composite/useNaiveFeedback';
import db from '@/services/db';

import styles from '../settings.module.less'

export default defineComponent({
  name: 'ClearData',
  setup() {
    const { t } = useI18n({ useScope: 'global' });
    const { dialog, message } = useNaiveFeedback();

    return () => <li class={cn(styles.item)} onClick={() => {
      dialog.warning({
        title: t('pages.settings.clearData.confirmDialog.title'),
        content: t('pages.settings.clearData.confirmDialog.content'),
        positiveText: t('common.yes'),
        negativeText: t('common.thinkAgain'),
        closable: false,
        maskClosable: false,
        onPositiveClick() {
          Promise.all([
            db.book.clearAllBooks(),
            db.bookcase.clearAllBookcases(),
            db.settings.clearAllSettings()
          ]).then(() => {
            message.success(t('pages.settings.clearData.confirmDialog.clearFinished'));
            setTimeout(() => {
              location.replace('./');
            }, 7e2);
          });
        }
      });
    }}>
      <span class={cn(styles.label)}>{t('pages.settings.clearData.label')}</span>
      <span class={cn(styles.value)}></span>
    </li>
  }
});
